*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --pink: #e86f54;
  --pink-hover: #e25b41;
  --pink-focus: #fa6f54;
  --mint: #3cbc81;
  --carmine: #e74a3b;
  --onyx: #111;
  --carbon: #5c5c5c;
  --iron: #d7d7d7;
  --night: #060606;
  --black: #000;
  --white: #fff;
  --black-6: #0009;
  --modal-shadow: #0000004d;
  --standard: .25s cubic-bezier(.4, 0, .2, 1);
}

@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
}

@font-face {
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@700&display=swap");
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");
}

@font-face {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 700;
  src: url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap");
}

body {
  background-color: var(--white);
  color: var(--black);
  font-family: Montserrat, sans-serif;
}

.section {
  margin: 0 16px 60px;
}

.link {
  font-style: normal;
  text-decoration: none;
}

.main-header {
  color: var(--black);
  text-align: center;
  font-size: 52px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.section-header {
  color: var(--black);
  align-items: center;
  gap: 16px;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  display: flex;
}

.header {
  background: var(--white-background);
}

.container {
  max-width: 480px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.header-nav {
  height: 35px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  background-color: #0000;
  border: none;
  display: flex;
}

.icon {
  fill: var(--black);
}

.tel-icon {
  display: flex;
}

.header-list {
  color: var(--black);
  justify-content: space-between;
  align-items: center;
  padding: 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  display: flex;
}

.list {
  list-style: none;
}

.header-list-nav {
  color: var(--white);
  padding: 24px 0;
}

.address-main {
  gap: 40px;
  margin: 0;
  padding: 24px 0;
  display: flex;
}

.address-list {
  color: var(--text-color);
}

.header-flex {
  display: none;
}

.mobile-menu-layout {
  margin: auto;
  padding: 16px 16px 0;
}

.mobile-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mobile-menu-layout button {
  background-color: #0000;
  border: none;
  margin-left: 20px;
}

.mobile-header-nav .header-list {
  flex-flow: column;
  justify-content: center;
  align-items: center;
  row-gap: 26px;
  margin: 0;
  padding: 0;
  display: flex;
}

.mobile-header-nav .header-list-nav {
  color: var(--white);
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.js-menu-container {
  display: none;
}

.js-menu-container.is-open {
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: #e86f54;
  flex-flow: column;
  padding: 124px 110px 180px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu-close {
  width: 100%;
  justify-content: flex-end;
  display: flex;
}

.button-modal-box {
  width: 24px;
  height: 24px;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.header-line {
  border-bottom: 1px solid var(--black-6);
  flex-grow: 1;
}

.flex-about {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin: 0 auto;
  padding-top: 32px;
  display: flex;
}

.p-about {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.5px;
}

.flex-store {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin: 0 auto;
  padding: 32px 0;
  display: flex;
}

.store {
  flex-basis: calc(50% - 8px);
}

.card-header {
  color: var(--black);
  padding: 8px 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.5px;
}

.card-p {
  color: var(--black-6);
  padding-bottom: 8px;
  font-size: 12px;
  font-weight: 600;
  line-height: 14.25px;
}

.btn-order {
  min-width: 208px;
  height: 44px;
  background-color: var(--pink);
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 38px;
  margin: 0 auto;
  font-family: Montserrat, sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  display: block;
}

.btn-order:hover {
  background-color: var(--pink-hover);
}

.btn-order:focus {
  background-color: var(--pink-focus);
}

.hero {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 auto 100px;
  display: flex;
  position: relative;
}

.flower-studio {
  width: 46px;
  height: 34px;
  color: #000;
  margin-right: 11px;
  font-size: 14px;
  font-weight: 400;
}

.hero-flower {
  width: 145px;
  height: 274px;
  position: relative;
}

.hero-shop-now-mobile {
  width: 64px;
  height: 76px;
  background-color: #0000;
  margin-left: 11px;
}

.hero-shop-now-tablet, .hero-shop-now-desktop {
  display: none;
}

.hero-svg {
  fill: currentColor;
  height: auto;
  transform-origin: center;
  width: 100%;
}

.anemone-logo {
  z-index: 1;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 90%;
  left: 0%;
}

.anemone-logo > p {
  width: 288px;
  height: 63px;
  font-size: 52px;
  font-weight: 400;
}

.anemone-logo-div {
  display: none;
}

.backdrop {
  background-color: var(--modal-shadow);
  z-index: 1;
  transition-property: transform, opacity, visibility;
  transition: var(--standard);
  position: fixed;
  inset: 0;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

.modal {
  text-align: center;
  max-width: 272px;
  width: 100%;
  background-color: var(--white);
  box-shadow: 0px 2px 1px 1px var(--modal-shadow);
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: 28px 16px 0;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-close {
  box-sizing: border-box;
  fill: var(--carbon);
  transition-property: fill;
  transition: var(--standard);
  width: 22px;
  height: 22px;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-close:hover, .modal-close:focus {
  fill: var(--night);
}

.contact-header {
  margin: 0 0 14px;
  font-size: 20px;
  font-weight: 600;
  line-height: normal;
}

.contact-p {
  color: var(--carbon);
  width: 240px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
}

.contact-field {
  width: 100%;
  display: inline-block;
  position: relative;
}

.contact-field-header {
  color: var(--carbon);
  flex-direction: row;
  margin: 14px 0 8px;
  padding: 0;
  font-size: 10px;
  font-weight: 500;
  line-height: normal;
  display: flex;
}

.contact-field-box {
  width: 100%;
  max-width: 240px;
  height: 40px;
  border: 1px solid var(--iron);
  color: var(--carbon);
  transition-property: border;
  transition: var(--standard);
  border-radius: 4px;
  outline: none;
  flex-shrink: 0;
  padding: 0 0 0 12px;
  font-size: 12px;
  font-weight: 500;
  position: relative;
}

.contact-field-box:hover, .contact-field-box:focus {
  border: 1px solid var(--carbon);
  transition-property: border;
  transition: var(--standard);
}

.contact-field-box::placeholder {
  color: var(--modal-shadow);
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
}

.contact-send {
  justify-content: center;
  margin: 24px 0 26px;
  display: flex;
}

.send-button {
  box-sizing: border-box;
  background-color: var(--pink);
  box-shadow: var(--modal-shadow);
  width: 240px;
  height: 37px;
  color: var(--white);
  border: 0;
  border-radius: 8px;
  flex-shrink: 0;
  margin: 0 0 4px;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
}

.send-button:hover, .send-button:focus {
  background-color: var(--pink-hover);
}

.send-button:focus {
  background-color: var(--pink-focus);
}

.thankYouMessage {
  display: none;
}

.thank-you-message {
  margin: 0 auto;
}

.plantsicon {
  width: 188px;
  height: 142px;
  margin: 0 0 10px;
  padding: 0;
}

.tymsg-h {
  color: var(--black);
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  line-height: normal;
  display: none;
}

.tymsg-p {
  color: var(--carbon);
  text-align: center;
  margin: 14px 0 18px;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
}

.send-button:disabled {
  opacity: .4;
  cursor: not-allowed;
}

.contact-continue {
  justify-content: center;
  margin: 24px 0 26px;
  display: flex;
}

.continue-button {
  box-sizing: border-box;
  background-color: var(--pink);
  box-shadow: var(--modal-shadow);
  width: 240px;
  height: 37px;
  color: var(--white);
  border: 0;
  border-radius: 8px;
  flex-shrink: 0;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
}

.continue-button:hover, .continue-button:focus {
  background-color: var(--pink-hover);
}

.continue-button:focus {
  background-color: var(--pink-focus);
}

.section-blog {
  padding-bottom: 60px;
}

.blog-photos {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 32px;
  display: flex;
}

.blog-photo {
  width: 136px;
  height: 181px;
}

.blog-photo > img {
  height: 100%;
}

.hidden-photo {
  display: none;
}

.blog-arrow-icons {
  flex-direction: row;
  justify-content: center;
  gap: 25px;
  margin-top: 4px;
  display: flex;
}

.blog-icon-arrow-container {
  width: 50px;
  height: 50px;
}

.blog-icon-arrow {
  width: 100%;
  height: 100%;
}

.blog-p-follow {
  text-align: center;
  color: var(--black);
  margin-top: 14px;
  margin-bottom: 24px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22em;
}

.btn-subscribe {
  min-width: 170px;
  height: 40px;
  background-color: var(--pink);
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 38px;
  margin: 0 auto;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  display: block;
}

.btn-subscribe:hover {
  background-color: var(--pink-hover);
}

.btn-subscribe:focus {
  background-color: var(--pink-focus);
}

.modal2 {
  text-align: center;
  max-width: 272px;
  width: 100%;
  background-color: var(--white);
  box-shadow: 0px 2px 1px 1px var(--modal-shadow);
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: 0 0 28px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sub-hero {
  width: 272px;
  height: 121px;
  object-fit: cover;
  border: 1px dotted #9acd32;
  display: flex;
}

.sub-header {
  color: var(--carbon);
  margin: 18px 0 14px;
  font-size: 19px;
  font-weight: 600;
  line-height: normal;
}

.sub-p {
  color: var(--carbon);
  width: 236px;
  margin: 14px 0 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
}

.sub-field {
  width: 100%;
  max-width: 236px;
}

.sub-field-header {
  color: var(--carbon);
  width: 236px;
  justify-content: start;
  margin: 18px 0 8px;
  padding: 0;
  font-size: 10px;
  font-weight: 500;
  line-height: normal;
  display: flex;
}

.sub-field-box {
  width: 100%;
  max-width: 236px;
  height: 40px;
  border: 1px solid var(--iron);
  color: var(--carbon);
  transition-property: border;
  transition: var(--standard);
  border-radius: 4px;
  outline: none;
  flex-shrink: 0;
  margin: 0 0 18px;
  padding: 0 0 0 12px;
  font-size: 12px;
  font-weight: 500;
  position: relative;
}

.sub-field-box:hover, .sub-field-box:focus {
  border: 1px solid var(--carbon);
  transition-property: border;
  transition: var(--standard);
}

.subscribe-button {
  box-sizing: border-box;
  background-color: var(--pink);
  box-shadow: var(--modal-shadow);
  width: 236px;
  height: 37px;
  color: var(--white);
  border: 0;
  border-radius: 8px;
  flex-shrink: 0;
  margin: 0 0 4px;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
}

.subscribe-button:hover, .subscribe-button:focus {
  background-color: var(--pink-hover);
}

.subscribe-button:focus {
  background-color: var(--pink-focus);
}

.delivery-section {
  background-color: var(--white);
  padding-bottom: 60px;
  padding-left: 0;
  padding-right: 0;
}

.delivery-svg-ellipse {
  fill: var(--white);
  stroke-width: 1px;
  stroke: var(--black);
  width: 40px;
  height: 40px;
  margin-right: 8px;
}

.delivery-order-list {
  flex-direction: column;
  row-gap: 14px;
  margin-top: 45px;
  display: flex;
}

.delivery-order-number {
  align-items: center;
  display: flex;
}

.delivery-svg-position {
  position: relative;
}

.number-in-svg {
  color: var(--black);
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  position: absolute;
  top: 8px;
  left: 16px;
}

.img {
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.delivery-image-position {
  margin-top: 26px;
}

.delivery-article {
  color: var(--black);
  max-width: 232px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.footer {
  height: 389px;
  background-color: var(--black);
  color: var(--white);
  font-family: var(--footer-font);
  justify-content: space-between;
  padding: 32px 16px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  display: flex;
}

.footer-header {
  margin: 0 0 18px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  display: flex;
}

.foot-box {
  width: 100%;
  flex-flow: wrap;
  justify-content: space-between;
  display: flex;
}

.footer-part1 {
  display: none;
}

.footer-part2 {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.footer-ot {
  margin: 0 0 6px;
}

.footer-part3 {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.footer-nav-item, .footer-tel {
  padding: 0 0 12px;
}

.footer-mail {
  width: 147px;
  flex-direction: column;
  padding: 0 0 12px;
  display: flex;
}

address {
  font-family: var(--footer-font);
  width: 147px;
  height: 2em;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.footer-sm {
  flex-direction: row;
  align-items: flex-start;
  margin: 0 57px 0 0;
  display: flex;
}

.footer-icon {
  fill: var(--white);
  vertical-align: top;
  margin-bottom: 18px;
}

.footer-sm-fb {
  margin: 0 0 0 14px;
}

@media (min-width: 768px) {
  .mobile-menu-layout {
    display: none;
  }

  .container {
    max-width: 768px;
    width: 100%;
  }

  .header-list {
    gap: 24px;
    margin: 0;
    padding: 16px 0 17px;
  }

  .header-flex {
    display: block;
  }

  .header-list-nav {
    color: var(--black);
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .header-nav {
    height: 57px;
  }

  .address {
    margin-left: 0;
  }

  .logo-div {
    margin-right: 70px;
  }

  .desktop-icon-div {
    display: none;
  }

  .tablet-icon-div {
    margin-left: 70px;
    display: block;
  }

  .header-div {
    padding: 24px 9px 0;
  }

  .section {
    margin: 0 24px 100px;
  }

  .section-header {
    font-size: 44px;
  }

  .flex-about {
    flex-direction: row;
    gap: 30px;
    padding-top: 30px;
  }

  .item-about {
    flex-basis: 50%;
  }

  .p-about {
    flex-basis: 50%;
    font-size: 20px;
    line-height: 24.38px;
  }

  .flex-store {
    gap: 32px;
    padding: 60px 0;
  }

  .card-header {
    align-items: center;
    gap: 8px;
    font-size: 32px;
    line-height: 39.01px;
    display: flex;
  }

  .card-line {
    border-bottom: 1px solid var(--black);
    flex-grow: 1;
  }

  .card-p {
    font-size: 24px;
    line-height: 29.26px;
  }

  .store {
    flex-basis: calc(50% - 16px);
  }

  .btn-order {
    min-width: 280px;
    height: 64px;
    font-size: 28px;
  }

  .section-blog {
    padding-bottom: 100px;
  }

  .blog-photos {
    gap: 32px;
    margin-top: 60px;
  }

  .blog-photo {
    width: 344px;
    height: 398px;
  }

  .blog-arrow-icons {
    margin-top: 8px;
  }

  .blog-icon-arrow-container {
    width: 61px;
    height: 61px;
  }

  .blog-p-follow {
    margin-top: 18px;
    margin-bottom: 32px;
    font-size: 36px;
  }

  .btn-subscribe {
    width: 280px;
    height: 64px;
    font-size: 28px;
  }

  .footer {
    height: auto;
    padding: 60px 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
  }

  .footer-header {
    margin: 0 0 18px;
    font-size: 20px;
    font-weight: 700;
    line-height: 1;
  }

  .footer-part2, .footer-part3 {
    width: calc(50% - 12px);
  }

  .footer-mail {
    width: 257px;
    flex-direction: row;
    display: flex;
  }

  .footer-sm {
    flex-direction: column;
    margin: 0 0 0 64px;
    display: flex;
  }

  .footer-sm-fb {
    margin: 0;
  }

  .hero {
    margin-bottom: 150px;
  }

  .flower-studio {
    width: 76px;
    height: 58px;
    margin-right: 49px;
    font-size: 24px;
    font-weight: 400;
  }

  .hero-flower {
    width: 390px;
    height: 636px;
  }

  .hero-shop-now-mobile {
